<template>
    <div>
        <p class="vui-page-title">-</p>
        <p class="mr-b10">size smallsmall,middle,larger</p>
        <v-radio>default</v-radio>
        <v-radio size="small">small</v-radio>
        <v-radio size="middle">middle</v-radio>
        <v-radio size="larger">larger</v-radio>
        <br>
        <p class="mr-b10">checked: booleanboolean!</p>
        <v-radio :checked="false">checkedfalse</v-radio>
        <v-radio :checked="true">checkedtrue</v-radio>
        <br>
        <p class="mr-b10">disbaled: booleanboolean!,</p>
        <v-radio>checkedfalse</v-radio>
        <v-radio :disabled="true">checkedtrue</v-radio>
        <v-radio :checked="true">checkedtrue</v-radio>
        <v-radio :checked="true" :disabled="true">checkedtrue</v-radio>
        <br>
        <p class="mr-b10">name</p>
        <v-radio name="radio11">radio11</v-radio>
        <v-radio name="radio22">radio22</v-radio>
        <br>
        <p class="mr-b10">name</p>
        <v-radio @onClick="onClick">click</v-radio>

        <p class="vui-page-title">-</p>
        <p class="mr-b10">sizename sizename</p>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" size="middle" name="group-radio1"></v-radio-group>
        <br>
        <p class="mr-b10">disabled true</p>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" disabled="true" name="group-radio9"></v-radio-group>
        <br>
        <p class="mr-b10">groupData text</p>
        <pre><code data-language="javascript">{{radioData}}</code></pre>
        <v-radio-group :groupData="radioData" name="group-radio2"></v-radio-group>
        <br>
        <p>checkedDatascheckedKey checkedDatasradiocheckedKeyindexcheckedDatagroupDatacheckedKey</p>
        <p class="mr-b10">checkedData3</p>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" name="group-radio3">checkedKey</v-radio-group>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" checkedKey="value" name="group-radio4">checkedKey=value</v-radio-group>
        <br>
        <p class="mr-b10">singleClickupdateCheckedDatas</p>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" name="group-radio5"></v-radio-group>
        <v-radio-group :groupData="radioData" :checkedData="checkedDatas" name="group-radio6" checkedKey="value" @onClick="singleClick"></v-radio-group>
    </div>
</template>

<script>
    export default {
        name: 'radio',
        data() {
            return {
                checked: true,
                radioData: [{
                    value: 1,
                    text: 'radio1'
                }, {
                    value: 2,
                    text: 'radio2'
                }, {
                    size: 'small',
                    value: 3,
                    text: 'radio3'
                }, {
                    value: 4,
                    text: 'radio4'
                }],
                checkedDatas: 3
            }
        },
        methods: {
            onClick: function (event, value, index) {
                console.log('value ==>', value);
                console.log('index ==>', index);
            },
            singleClick: function (event, value, index) {
                console.log('value ==>', value);
                console.log('index ==>', index);
            }
        }
    }

</script>

