<template>
    <div>
        <p class="vui-page-title"></p>
        <v-input v-model="defaultVal"></v-input>

        <p class="vui-page-title">width</p>
        <v-input width="300px" value="width:300px"></v-input>
        <br>
        <v-input width="30%" value="width:30%"></v-input>
        <br>
        <v-input width="30em" value="width:30em"></v-input>
        <br>
        <v-input width="30rem" value="width:30rem"></v-input>

        <p class="vui-page-title">valuev-modelvaluev-model</p>
        <v-input v-model="vModelVal"></v-input>
        <v-input v-model="vModelVal"></v-input>
        <br>
        <v-input :value="val"></v-input>
        <v-input :value="val"></v-input>

        <p class="vui-page-title">disabledreadolnyclassstyle</p>
        <v-input disabled value="disabled"></v-input>
        <br>
        <v-input readonly value="readonly"></v-input>
        <br>
        <v-input class="test-input-class" value="classtest-input-class"></v-input>
        <br>
        <v-input style="color:#f30;" value="#f30"></v-input>
        <br>
        <v-input data-id="233" width="80%" value="data-id233"></v-input>

        <p class="vui-page-title">focusblurchangeinputeventvalue</p>
        <v-input disabled value="disabled" @focus="focusEvent" @blur="blurEvent" @input="changeEvent" @change="inputsEvent"></v-input>
        <br>
        <v-input readonly value="readonlychangeinput" width="80%" @focus="focusEvent" @blur="blurEvent" @input="changeEvent"
            @change="inputsEvent"></v-input>
        <br>
        <v-input value="focusEvent" @focus="focusEvent"></v-input>
        <br>
        <v-input value="blurEvent" @blur="blurEvent"></v-input>
        <br>
        <v-input value="changeEvent" @input="changeEvent"></v-input>
        <br>
        <v-input value="changeEvent" @change="changeEvent"></v-input>
        <br>
    </div>
</template>

<script>
    export default {
        name: 'input',
        data() {
            return {
                defaultVal: '',
                vModelVal: 'v-model',
                val: 'value'
            }
        },
        methods: {
            focusEvent(event, value) {
                console.log('==>', value)
            },
            blurEvent(event, value) {
                console.log('==>', value)
            },
            changeEvent(event, value) {
                console.log('change==>', value)
            },
            inputsEvent(event, value) {
                console.log('input==>', value)
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    .box {
        display: inline-block;
        width: 100px;
        height: 50px;
        background: #795da3;

        img {
            width: 100%;
            height: 100%;
            background: #999;
        }
        .file {
            object-fit: fill;
        }
        .contain {
            object-fit: contain;
            object-position: 50% 50%;
        }
        .cover {
            object-fit: cover;
        }
        .none {
            object-fit: none;
        }
        .scale-down {
            object-fit: scale-down;
        }
    }
</style>



