<template>
    <div>
        <p class="vui-page-title">-</p>
        <p class="mr-b10">size smallsmall,middle,larger</p>
        <v-checkbox>default</v-checkbox>
        <v-checkbox size="small">small</v-checkbox>
        <v-checkbox size="middle">middle</v-checkbox>
        <v-checkbox size="larger">larger</v-checkbox>
        <br>
        <p class="mr-b10">checked: booleanboolean!</p>
        <v-checkbox :checked="false">checkedfalse</v-checkbox>
        <v-checkbox :checked="true">checkedtrue</v-checkbox>
        <br>
        <p class="mr-b10">disbaled: booleanboolean!,</p>
        <v-checkbox>checkedfalse</v-checkbox>
        <v-checkbox :disabled="true">checkedtrue</v-checkbox>
        <v-checkbox :checked="true">checkedtrue</v-checkbox>
        <v-checkbox :checked="true" :disabled="true">checkedtrue</v-checkbox>
        <br>
        <p class="mr-b10">name</p>
        <v-checkbox name="checkbox1">checkbox1</v-checkbox>
        <v-checkbox name="checkbox2">checkbox2</v-checkbox>
        <br>
        <p class="mr-b10">name</p>
        <v-checkbox @onClick="onClick">click</v-checkbox>

        <p class="vui-page-title">-</p>
        <p class="mr-b10">sizename sizename</p>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" size="middle" name="group-checkbox"></v-checkbox-group>
        <br>
        <p class="mr-b10">disabled true</p>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" disabled="true" name="group-checkbox"></v-checkbox-group>
        <br>
        <p class="mr-b10">groupData ,text</p>
        <pre><code data-language="javascript">{{checkboxData}}</code></pre>
        <v-checkbox-group :groupData="checkboxData"></v-checkbox-group>
        <br>
        <p>checkedDatascheckedKey checkedDatascheckboxcheckedKeyindexcheckedDatagroupDatacheckedKey</p>
        <p class="mr-b10">checkedData[1,2,3]</p>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" @updateCheckedDatas="updateCheckedDatas">checkedKey</v-checkbox-group>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" checkedKey="value" @updateCheckedDatas="updateCheckedDatas">checkedKey=value</v-checkbox-group>
        <br>
        <p class="mr-b10">singleClickupdateCheckedDatas</p>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" @updateCheckedDatas="updateCheckedDatas"></v-checkbox-group>
        <v-checkbox-group :groupData="checkboxData" :checkedData="checkedDatas" @singleClick="singleClick" checkedKey="value"></v-checkbox-group>
    </div>
</template>

<script>
    export default {
        name: 'checkbox',
        data() {
            return {
                checked: true,
                checkboxData: [{
                    value: 1,
                    text: 'checkbox1'
                }, {
                    value: 2,
                    text: 'checkbox2'
                }, {
                    size: 'small',
                    value: 3,
                    text: 'checkbox3'
                }, {
                    value: 4,
                    text: 'checkbox4'
                }],
                checkedDatas: [1, 2, 3]
            }
        },
        methods: {
            onClick: function (event, checked) {
                console.log('checked inner==>', checked);
                alert(checked ? '' : '');
            },
            singleClick: function (event, checked, value, index) {
                console.log('checked ==>', checked);
                console.log('value ==>', value);
                console.log('index ==>', index);
            },
            updateCheckedDatas: function (checkedDatas) {
                console.log('checkedDatas=>', checkedDatas)
                alert('' + checkedDatas.join());
            }
        }
    }

</script>

<!-- Add "scoped " attribute to limit CSS to this component only -->
<style scoped lang="scss">
    pre {
        position: relative;
        word-wrap: break-word;
        white-space: pre-wrap;
        padding: 6px 10px;
        background-color: #f5f5f5;
        font-size: 14px;
        border: 1px solid #ccc;
        border-radius: 4px;
        overflow: hidden;
        color: $font-color-main;
    }
</style>



