<template>
    <div class="vui-radio-group">
        <radio v-for="(item, index) in selfGroupData" :disabled="disabled" :key="index" :index="index" :size="item.size" :name="name" :checked="item.selfChecked"
            :value="getVal(item,index)" @onClick="onClick">{{item.text}}</radio>
    </div>
</template>

<script>

    import Radio from './index';
    export default {
        name: 'vRadioGroup',
        data() {
            return {
                selfGroupData: this.getRadioData()
            }
        },
        components: {
            Radio
        },
        props: {
            //radio
            groupData: {
                type: Array,
                default() {
                    return [];
                }
            },
            //radioname
            name: {
                type: String,
                default: 'radio'
            },
            //
            checkedData: {
                type: [String, Number],
                default: undefined
            },
            //groupDataradio
            checkedKey: {
                type: String,
                default: 'index'
            },
            disabled: {
                default: undefined
            }
        },
        methods: {
            //groupData
            getRadioData() {
                const _this = this, checkedKey = _this.checkedKey, checkedData = _this.checkedData;
                let selfGroupData = [];
                this.groupData.forEach((radio, index) => {
                    selfGroupData.push({ ...radio, ...{ selfChecked: this.getVal(radio, index) === this.checkedData } });
                });
                return selfGroupData;
            },
            //map
            getVal(item, index) {
                return this.checkedKey !== 'index' ? item[this.checkedKey] : index
            },
            //
            onClick(event, value, index) {
                this.$emit('onClick', event, value, index);
            }
        }
    }

</script>

<style scoped>
</style>



